import React from 'react';
import {Link} from 'react-router-dom';

const ArticleLink  = ({title, to, content}) => 
                    (<Link className = "article-list-item" to={to}>
                        <h3>{title}</h3>
                        <p>{content}</p>
                    </Link>)


class ArticleList extends React.Component{
    render(){
        const articleContent = this.props.articles

        return(
            <React.Fragment>
                {articleContent.map(
                    (article, index) => 
                    (<ArticleLink   title={article.title} 
                                    to={'/article/' + article.name} 
                                    content={article.content[0].substring(0,150) + "..."}
                                    key={index}/>)
                )}
            </React.Fragment>
        )
    }
}

export default ArticleList;