import React, { useState } from 'react';
import articleContent from '../tests/article-content';
import { without, findIndex } from 'lodash';
import ArticleList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';


const UpvoteSection = ({upvote, onUpvoteClick}) => {
    return (
        <div id="upvote-section">
            <button onClick={() => onUpvoteClick()}>Add Upvote</button> &nbsp;
            <p>This article has been upvoted {upvote} times</p>
        </div>
    )
}

const CommentSection = ({comments}) => {
    return (
        <React.Fragment>
            {comments.map(
                (item, index) =>
                (<div className="comment" key={index}>
                    <h4>{item.username}</h4>
                    <p>{item.comment}</p>
                </div>)
            )}
        </React.Fragment>
    )
}

const AddCommentForm  = ({onAddComment}) => {
    const [userName, setUserName] = useState('')
    const [comment, setComment] = useState('')
    return (
        <div className="add-comment-form">
            <label>
                Name :
                <input  type="text" 
                        value={userName} 
                        onChange={e => setUserName(e.target.value)}/>
            </label>
            <label>
                Comment :
                <textarea   rows="4"
                            cols="5"
                            value={comment}
                            onChange={e => setComment(e.target.value)} />
            </label>
            <button onClick={() => {onAddComment(userName, comment); setComment(''); setUserName('')}}>Add Comment</button>
        </div> 
    )
}


class ArticlePage extends React.Component{
    
    state = {
        upvote : 0,
        comments : [],
    }

    componentDidMount(){
        this.fetchArticle()
    }

    componentDidUpdate(prevProps, prevStates){
        // If these is a difference between past can old props of name
        if (this.props.match.params.name === prevProps.match.params.name) return
        this.fetchArticle()
    }

    fetchArticle = async () => {
        const name = this.props.match.params.name
        await fetch(`http://localhost:8000/api/article/${name}`)
        .then(resp => resp.json())
        .then(resp => {
            this.setState({upvote : resp.upvote, comments : resp.comments})
        })
        .catch(err => console.log(err))
    }

    onUpvoteClick = async () => {
        const name = this.props.match.params.name
        await fetch(`http://localhost:8000/api/article/${name}/upvote`, {method:'post'})
        .then(resp => resp.json())
        .then(resp => {
            this.setState({upvote : resp.upvote, comments : resp.comments})
        })
        .catch(err => console.log(err))
    }

    addComment = async (userName, comment) => {
        const name = this.props.match.params.name
        await fetch(`http://localhost:8000/api/article/${name}/add-comment`, {
            method:'post',
            body: JSON.stringify({username : userName, comment : comment}),
            headers : {
                'Content-Type' : 'application/json'
            }
        })
        .then(resp => resp.json())
        .then(resp => {
            this.setState({upvote : resp.upvote, comments : resp.comments})
        })
        .catch(err => console.log(err))
    }

    render(){
        const name = this.props.match.params.name
        const articleIdx = findIndex(articleContent, {name : name})
        if(articleIdx < 0) return (<NotFoundPage />)
        const article = articleContent[articleIdx]
        const otherArticles = without(articleContent, article)
        return(
            <React.Fragment>
                <h1>{article.title}</h1>
                <UpvoteSection upvote={this.state.upvote} onUpvoteClick={this.onUpvoteClick}/>
                {article.content.map(
                    (paragraph, index) => 
                    (<p key={index}>
                        {paragraph}
                    </p>)
                )}

                <h1>Other Articles</h1>
                <ArticleList articles={otherArticles}/>

                <h3>Comments Section</h3>
                <CommentSection comments={this.state.comments}/>
                <h3>Add A Comment</h3>
                <AddCommentForm onAddComment={this.addComment}/>
            </React.Fragment>
        )
    }
}

export default ArticlePage;
    
