import React from 'react';
import {Link} from 'react-router-dom';

const NavBar = () => {
    const navItems = [
        {name: 'Home', link : '/'},
        {name: 'About', link : '/about'},
        {name: 'Articles', link : '/list'},
    ]

    return (
        <nav>
            <ul>
                {navItems.map(
                    (item, index) => 
                    (<li key={index}>
                        <Link to={item.link}>{item.name}</Link>
                    </li>)
                )}
            </ul>
        </nav>
    )
}

export default NavBar;